import React from 'react';
import Tilt from 'react-parallax-tilt';
import DON from './logo-don.png';
import './Logo.css';

const Logo = () => {
	return(
		<div className='ma4 mt0' >
			<Tilt className='br2 shadow-2 Tilt' style={{ height: 100, width: 250}}>
		    	<div className='pa3'> 
		    		<img style={{paddingTop:'5px'}} alt='logo' src={DON}/>
		    	</div>
		    </Tilt>
		</div>
	);
}

export default Logo;