import React from 'react';
import Product from './first-product.jpg';

const Card = () => {
	return(
		<article class="br2 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 center" style = {{display: 'flex', flexWrap: 'wrap'}}>
		  <img src={Product} class="db w-100 br2 br--top" alt="Photo of a kitten looking menacing." />
		  <div class="pa2 ph3-ns pb3-ns" style = {{display: 'flex', flexWrap: 'wrap'}}>
		    <div class="dt w-100 mt1">
		      <div class="dtc">
		        <h1 class="f5 f4-ns mv0">Name Product</h1>
		      </div>
		      <div class="dtc tr">
		        <h2 class="f5 mv0">$00.00</h2>
		      </div>
		    </div>
		    <p class="f6 lh-copy measure mt2 mid-gray">
		   		Product description
		    </p>
		  </div>
		</article>
	);
}

export default Card;