import React, {Component} from 'react';
import Navigation from './components/Navigation/Navigation';
// import Logo from './components/Logo/Logo';
// import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import SignIn from './components/SignIn/SignIn';
import Register from './components/Register/Register';
// import Rank from './components/Rank/Rank';
import Particle from './components/Particle/Particle';
import Publicity from './components/Publicity/Publicity';
import ProductosMain from './components/ProductosMain/Productos';
// import FaceRecognition from './components/FaceRecognition/FaceRecognition';
// import Clarifai from 'clarifai';
import './App.css';

// const app = new Clarifai.App({
//  apiKey: '818c118a63af437b9fe531848347a90d'
// });

class App extends Component {
  constructor(){
    super();
    this.state={
      input:'',
      imageUrl: '',
      box: {},
      route: 'signin',
      isSignedIn: false
    }
  }

  valoresImaginarios = () => {
    const bottomRow = 288.161;
    const leftCol = 166.954;
    const rightCol = 167.376;
    const topRow = 177.729;

    return {
      leftCol: leftCol,
      topRow: topRow,
      rightCol: rightCol,
      bottomRow: bottomRow
    }
  }

  displayFaceBox = (box) => {
    this.setState({box: box});
  }

  // Obtener el valor
  onInputChange = (event) => {
    this.setState({input:event.target.value});
  }


  onButtonSubmit = () =>{
    this.setState({imageUrl: this.state.input});
    this.displayFaceBox(this.valoresImaginarios());
  }

  onRouteChange = (route) => {
    if (route === 'signout'){
      this.setState({isSignedIn: false})
    }else if (route === 'home'){
      this.setState({isSignedIn: true})
    }
    this.setState({route: route})
  }

  render(){
    const {isSignedIn, imageUrl, route, box} = this.state;
    return (
      <div className="App">
        <Particle className='particles'/>
        <Navigation isSignedIn={isSignedIn} onRouteChange={this.onRouteChange}/>
        {route === 'home'
        ? <div>
            <Publicity />
            <ProductosMain />

            {/*
            <Rank />
            <ImageLinkForm onInputChange={this.onInputChange} onButtonSubmit={this.onButtonSubmit} />
            <FaceRecognition box={box} imageUrl={imageUrl}/>*/}
          </div>
        : (
          route === 'signin'
          ?<SignIn onRouteChange={this.onRouteChange}/>
          :<Register onRouteChange={this.onRouteChange}/>
          )
        }
      </div>
    );
  }
}

export default App;
