import React from 'react';
import Logo from '../Logo/Logo.js';

const Navigation = ({onRouteChange,isSignedIn}) => {

		// Para un estilo con flex 
		if(isSignedIn){
			return(
				<nav style = {{display: 'flex', justifyContent: 'space-between', background: 'white', alignItems: 'center'}}>
					<div style = {{height: '100px'}}>
						<Logo />
					</div>
					<div style = {{display: 'flex', justifyContent: 'flex-end'}}>
						<p onClick={() => onRouteChange('category')} className='f3 link dim black underline pa3 pointer'>Category</p>
						<p onClick={() => onRouteChange('signout')} className='f3 link dim black underline pa3 pointer'>Sign Out</p>
					</div>
				</nav>
			);
		}else{
			return(
				<nav style = {{display: 'flex', justifyContent: 'flex-end'}}>
					<p onClick={() => onRouteChange('signin')} className='f3 link dim black underline pa3 pointer'>Sign In</p>
					<p onClick={() => onRouteChange('register')} className='f3 link dim black underline pa3 pointer'>Register</p>
				</nav>
			);
		}
}

export default Navigation;